import React, { useContext } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import Typist from 'react-typist';
import { ThemeContext } from 'providers/ThemeProvider';
import { Header } from 'components/theme';
import { Container, Button } from 'components/common';
import { resumeLink } from 'data/config';
import { Wrapper, IntroWrapper, Details, JobPosition as StyledJobPosition } from './styles';

const jobPositionTheme = {
  dark: 'Node.js',
  light: 'React',
};

const JobPosition = () => {
  const { theme, toggleTheme } = useContext(ThemeContext);

  return (
    <StyledJobPosition theme={theme} onClick={() => toggleTheme()}>
      {jobPositionTheme[theme]}
    </StyledJobPosition>
  );
};

export const Intro = () => {
  const { theme } = useContext(ThemeContext);

  return (
    <Wrapper>
      {/* <Header /> */}
      <IntroWrapper as={Container}>
        <Details theme={theme}>
          <Typist cursor={{ show: false }}>
            <h1>Hi There!</h1>
            <Typist.Delay ms={500} />
            <h4>
              Im Bohdan Andrianov and Im a <JobPosition /> developer
            </h4>
          </Typist>
          <Button
            onClick={() => {
              const win = window.open(resumeLink, '_blank');
              win.focus();
            }}
          >
            My Resume
          </Button>
        </Details>
      </IntroWrapper>
    </Wrapper>
  );
};
